.search-container{
    display: flex;
    justify-content: center;
    margin: 10px;
}
.search-input{
    padding: 8px 15px;
    font-size: 15px;
    width: 250px;
    border-width: 0;
    background-color: #9885e7;
    color: #e7e9f0;
    border-radius: 50px;
    box-shadow: 0 0 5px rgba(255,255,255,0.4);
}    

.search-input::placeholder{
    color: #e7e9f0;
    text-align: center;
}
.search-input:focus {
    outline: none;
    text-align: center;
}

